@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=DotGothic16&display=swap');

/* Stili di base */
body {
  margin: 0;
  padding: 0;
  font-family: 'Courier New', Courier, monospace;
  min-height: 100vh;
  overflow-x: hidden;
}

/* Dark mode styles */
.dark {
    background-color: #080c13; /* Aggiornato il colore di sfondo della dark mode */
    color: theme('colors.dark.text');
}

.dark h1, .dark h2, .dark h3 {
  color: theme('colors.dark.highlight');
  transition: text-shadow 0.3s ease;
}

.dark h1:hover, .dark h2:hover, .dark h3:hover {
  text-shadow: 0 0 12px rgba(0, 255, 127, 0.6);
}

.dark .text-primary {
  color: theme('colors.dark.highlight');
}

.dark .text-action {
  color: theme('colors.dark.action');
}

.dark .btn-primary {
  background-color: theme('colors.dark.button');
  color: theme('colors.dark.button-text');
}

.dark .btn-primary:hover {
  background-color: theme('colors.dark.button');
  filter: brightness(1.1);
}

.dark .card-bg {
  background-color: rgba(38, 38, 38, 0.7);
  backdrop-filter: blur(5px);
}

.dark .card-bg:hover {
  background-color: rgba(38, 38, 38, 0.85);
}

.dark .navbar-gradient, .dark .footer-gradient {
  background: linear-gradient(0deg, theme('colors.dark.accent') 85%, rgba(2, 181, 94, 0) 100%);
}

/* Light mode styles */
.light {
  background-color: theme('colors.light.bg');
  color: theme('colors.light.text');
}

.light h1, .light h2, .light h3 {
  color: theme('colors.light.highlight');
  transition: text-shadow 0.3s ease;
}

.light h1:hover, .light h2:hover, .light h3:hover {
  text-shadow: 0 0 12px rgba(0, 143, 79, 0.3);
}

.light .text-primary {
  color: theme('colors.light.highlight');
}

.light .text-action {
  color: theme('colors.light.action');
}

.light .btn-primary {
  background-color: theme('colors.light.button');
  color: theme('colors.light.button-text');
}

.light .btn-primary:hover {
  background-color: theme('colors.light.button');
  filter: brightness(1.1);
}

.light .card-bg {
  background-color: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(5px);
  border: 1px solid rgba(0, 143, 79, 0.1);
}

.light .card-bg:hover {
  background-color: rgba(255, 255, 255, 0.95);
  border-color: rgba(0, 143, 79, 0.2);
}

.light .navbar-gradient, .light .footer-gradient {
  background: linear-gradient(0deg, theme('colors.light.accent') 85%, rgba(161, 232, 217, 0) 100%);
}

/* Animazioni */
@keyframes pulse-custom {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(59, 130, 246, 0.7);
  }
  70% {
    transform: scale(1.05);
    box-shadow: 0 0 0 10px rgba(59, 130, 246, 0);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(59, 130, 246, 0);
  }
}

.pulse-animation {
  animation: pulse-custom 2s infinite;
}

.pulse-animation:hover {
  animation: none;
}

@keyframes slideInFade {
  0% {
    opacity: 0;
    transform: translateX(50px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.slide-in-section {
  animation: slideInFade 1.5s ease-out forwards;
  width: 100%;
}

.slide-in-section h1 {
  opacity: 0;
  animation: slideInFade 1.5s ease-out forwards;
  animation-delay: 0.5s;
}

.slide-in-section p {
  opacity: 0;
  animation: slideInFade 1.5s ease-out forwards;
  animation-delay: 1s;
}

@keyframes dealCard {
  0% {
    opacity: 0;
    transform: translateY(-100px) translateX(-50px) rotate(-20deg);
  }
  60% {
    transform: translateY(20px) translateX(10px) rotate(5deg);
  }
  80% {
    transform: translateY(-10px) translateX(-5px) rotate(-2deg);
  }
  100% {
    opacity: 1;
    transform: translateY(0) translateX(0) rotate(0);
  }
}

.card-trigger {
  opacity: 0;
  width: 100%;
}

.card-animate {
  animation: dealCard 1.5s cubic-bezier(0.34, 1.56, 0.64, 1) forwards;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-20px); }
}

.floating-element {
  animation: float 3s ease-in-out infinite;
}

/* Animated shapes updated with new colors */
.animated-circle {
  width: 200px;
  height: 200px;
  background: linear-gradient(45deg, var(--accent-color, #00FF7F), #4F4A4A);
  border-radius: 50%;
  animation: gradient-spin 8s linear infinite;
}

.animated-triangle {
  width: 0;
  height: 0;
  border-left: 100px solid transparent;
  border-right: 100px solid transparent;
  border-bottom: 173.2px solid var(--accent-color, #00FF7F);
  position: relative;
  animation: gradient-spin 8s linear infinite;
}

.animated-square {
  width: 200px;
  height: 200px;
  background: linear-gradient(45deg, var(--accent-color, #00FF7F), #4F4A4A);
  animation: gradient-spin 8s linear infinite;
}

@keyframes gradient-spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Set color variables based on theme */
.dark {
  --accent-color: theme('colors.dark.highlight');
  --action-color: theme('colors.dark.action');
}

.light {
  --accent-color: theme('colors.light.highlight');
  --action-color: theme('colors.light.action');
}

/* Enhanced matrix effect */
.enhanced-matrix-effect {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  pointer-events: none;
}

.dark .enhanced-matrix-effect canvas {
  opacity: 0.7;
}

.light .enhanced-matrix-effect canvas {
  opacity: 0.3;
}

/* Media queries for responsive design */
@media (max-width: 768px) {
  .slide-in-section {
    animation-delay: 0.2s;
  }
  
  .slide-in-section h1 {
    animation-delay: 0.4s;
  }
  
  .slide-in-section p {
    animation-delay: 0.6s;
  }
  
  @keyframes dealCard {
    0% {
      opacity: 0;
      transform: translateY(-50px);
    }
    60% {
      transform: translateY(10px);
    }
    80% {
      transform: translateY(-5px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes float {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-10px); }
  }
  
  .navbar-gradient, .footer-gradient {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}
