/* Stili per la navbar fissa e arrotondata */
.navbar-gradient {
  position: relative;
  background: linear-gradient(0deg, var(--accent-color, #00FF7F) 85%, rgba(5, 150, 105, 0) 100%);
  border-radius: 0.75rem;
  backdrop-filter: blur(10px);
  z-index: 100;
  transition: all 0.3s ease;
}

/* Transizione durante lo scroll */
.navbar-scrolled {
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(15px);
}

/* Aggiunta di un effetto glossy in dark mode */
.dark .navbar-gradient {
  background: linear-gradient(0deg, rgba(2, 181, 94, 0.8) 85%, rgba(2, 181, 94, 0) 100%);
  border: 1px solid rgba(0, 255, 127, 0.1);
}

/* Aggiunta di un effetto glossy in light mode */
.light .navbar-gradient {
  background: linear-gradient(0deg, rgba(161, 232, 217, 0.8) 85%, rgba(161, 232, 217, 0) 100%);
  border: 1px solid rgba(0, 143, 79, 0.1);
}

/* Media query per dispositivi più piccoli */
@media (max-width: 1440px) {
  .navbar-gradient {
    border-radius: 0.5rem;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    width: calc(100% - 1rem);
  }
}

/* Media query per dispositivi mobili */
@media (max-width: 768px) {
  .navbar-gradient {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}
