/* Enhanced WikiDEDIA Styles to match homepage */

/* Base styles */
.wikidedia-container {
  --accent-color: #00FF7F;
  --accent-dark: #02B55E; 
  --bg-color: #080C13;
  --text-color: #FFFFFF;
  --card-bg: rgba(38, 38, 38, 0.7);
  font-family: 'Courier New', monospace;
  background-color: var(--bg-color);
  color: var(--text-color);
  min-height: 100vh;
}

/* Matrix background effect */
.matrix-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  pointer-events: none;
}

.matrix-canvas {
  opacity: 0.7;
}

/* Header styles */
.wiki-header {
  position: sticky;
  top: 0;
  background: linear-gradient(90deg, var(--accent-dark), var(--bg-color));
  border-radius: 0.75rem;
  padding: 1rem;
  margin: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

.wiki-header a {
  display: flex;
  align-items: center;
  color: var(--accent-color);
  text-decoration: none;
  transition: color 0.3s;
}

.wiki-header a:hover {
  color: white;
}

.wiki-title {
  font-size: 1.75rem;
  color: var(--accent-color);
  text-shadow: 0 0 10px rgba(0, 255, 127, 0.5);
}

/* Main layout */
.wiki-layout {
  display: flex;
  height: calc(100vh - 6rem);
  margin-top: 1rem;
}

/* Sidebar styles */
.wiki-sidebar {
  width: 250px;
  background-color: rgba(0, 0, 0, 0.7);
  border-right: 1px solid rgba(0, 255, 127, 0.3);
  padding: 1rem;
  overflow-y: auto;
}

.wiki-sidebar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid rgba(0, 255, 127, 0.3);
}

.wiki-sidebar-title {
  color: var(--accent-color);
  font-size: 1.2rem;
}

.wiki-chapter {
  margin-bottom: 0.75rem;
}

.wiki-chapter-header {
  display: flex;
  align-items: center;
  padding: 0.5rem;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: background-color 0.2s;
}

.wiki-chapter-header:hover {
  background-color: rgba(0, 255, 127, 0.1);
}

.wiki-chapter-header.active {
  background-color: rgba(0, 255, 127, 0.2);
}

.wiki-chapter-title {
  color: var(--accent-color);
  font-weight: 600;
  display: flex;
  align-items: center;
}

.wiki-page-list {
  list-style: none;
  padding-left: 1.5rem;
  margin: 0.5rem 0;
}

.wiki-page-item {
  margin: 0.25rem 0;
}

.wiki-page-link {
  display: flex;
  align-items: center;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: background-color 0.2s;
  color: rgba(0, 255, 127, 0.8);
}

.wiki-page-link:hover {
  background-color: rgba(0, 255, 127, 0.1);
}

.wiki-page-link.active {
  background-color: rgba(0, 255, 127, 0.2);
  color: var(--accent-color);
}

/* Content area styles */
.wiki-content-area {
  flex: 1;
  padding: 1.5rem;
  overflow-y: auto;
  background-color: rgba(0, 0, 0, 0.6);
}

.wiki-page-content {
  max-width: 900px;
  margin: 0 auto;
}

.wiki-page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid rgba(0, 255, 127, 0.3);
}

.wiki-page-title {
  font-size: 1.75rem;
  color: var(--accent-color);
  margin: 0;
}

.wiki-page-actions {
  display: flex;
  gap: 0.5rem;
}

.wiki-action-button {
  display: flex;
  align-items: center;
  padding: 0.5rem 0.75rem;
  background-color: rgba(0, 255, 127, 0.1);
  border: 1px solid rgba(0, 255, 127, 0.3);
  border-radius: 0.25rem;
  color: var(--accent-color);
  cursor: pointer;
  transition: all 0.2s;
}

.wiki-action-button:hover {
  background-color: rgba(0, 255, 127, 0.2);
  box-shadow: 0 0 10px rgba(0, 255, 127, 0.3);
}

/* Markdown content styling */
.wiki-markdown h1 {
  font-size: 2rem;
  color: var(--accent-color);
  border-bottom: 2px solid rgba(0, 255, 127, 0.3);
  padding-bottom: 0.5rem;
  margin-top: 2rem;
  margin-bottom: 1rem;
}

.wiki-markdown h2 {
  font-size: 1.5rem;
  color: var(--accent-color);
  margin-top: 1.5rem;
  margin-bottom: 1rem;
}

.wiki-markdown h3 {
  font-size: 1.25rem;
  color: var(--accent-color);
  margin-top: 1.5rem;
  margin-bottom: 0.75rem;
}

.wiki-markdown p {
  margin-bottom: 1rem;
  color: #e0e0e0;
  line-height: 1.6;
}

.wiki-markdown ul, 
.wiki-markdown ol {
  margin-left: 1.5rem;
  margin-bottom: 1rem;
  color: #e0e0e0;
}

.wiki-markdown li {
  margin-bottom: 0.5rem;
}

/* Code highlighting */
.wiki-code-block {
  margin: 1.5rem 0;
  border-radius: 0.5rem;
  overflow: hidden;
  border: 1px solid rgba(0, 255, 127, 0.3);
  background-color: rgba(0, 0, 0, 0.7);
}

.wiki-code-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  background-color: rgba(0, 255, 127, 0.1);
  border-bottom: 1px solid rgba(0, 255, 127, 0.3);
}

.wiki-code-language {
  color: var(--accent-color);
  font-weight: 600;
}

.wiki-code {
  padding: 1rem;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  line-height: 1.5;
  overflow-x: auto;
  color: #e0e0e0;
}

/* Animations */
@keyframes pulse-glow {
  0%, 100% {
    box-shadow: 0 0 5px rgba(0, 255, 127, 0.3);
  }
  50% {
    box-shadow: 0 0 15px rgba(0, 255, 127, 0.6);
  }
}

.pulse-glow {
  animation: pulse-glow 2s infinite;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .wiki-layout {
    flex-direction: column;
    height: auto;
  }
  
  .wiki-sidebar {
    width: 100%;
    max-height: 300px;
    border-right: none;
    border-bottom: 1px solid rgba(0, 255, 127, 0.3);
  }
  
  .wiki-page-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .wiki-page-actions {
    margin-top: 1rem;
  }
}